@charset "UTF-8";

html {
  -webkit-tap-highlight-color: transparent;
}

body {
  width: 100%;
  font-family: "Arial","微软雅黑";
  font-size: 0.16rem;
}

button,
input,
select,
textarea {
  outline: none;
}

body,
dd,
dl,
dt,
h1,
h2,
h3,
input,
p,
select,
textarea,
ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

img {
  border: none;
  vertical-align: top;
}

a:link,
a:visited {
  text-decoration: none;
}

a:active,
a:focus,
a:hover {
  text-decoration: none;
}

.hide {
  display: none;
}

.clearfix {
  zoom: 1;
}

.clearfix:after {
  display: block;
  content: '';
  clear: both;
}

#layout {
  max-width: 10rem;
  width: 100%;
  margin: 0 auto;
}

header {
  width: 100%;
  background: #e9546b;
  position: relative;
}

header .logo {
  display: inline-block;
  float: left;
  width: 1.693333rem;
  height: 0.533333rem;
  margin-left: 0.266667rem;
  padding: 0.266667rem 0;
}

header img {
  width: 100%;
}

header .link {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0.266667rem;
  margin: auto;
  display: table;
  color: #fff;
}

[data-dpr="1"] header .link {
  font-size: 7px;
}

[data-dpr="2"] header .link {
  font-size: 14px;
}

[data-dpr="3"] header .link {
  font-size: 21px;
}

.content {
  width: 100%;
}

.content img {
  width: 100%;
}

footer {
  padding: 0.08rem 0 0.026667rem;
  text-align: center;
  background: #fff;
}

footer img {
  width: 1.866667rem;
  height: 1.866667rem;
}

footer p {
  font-weight: normal;
  font-size: 0.266667rem;
}

footer p.phone {
  line-height: 0.266667rem;
  font-size: 0.24rem;
  margin-top: 0.133333rem;
}
/*# sourceMappingURL=common.css.map */
